<template>
<div>
  <b-alert show variant="secondary"><i class="icon-info icons"></i>&nbsp;Showing only inactive (deactivated) caregivers</b-alert>
  <!-- <div class="add-remove-section">
    <b-button variant="primary" size="sm" class="btn-pill btn-add-remove-classes"><i class="fa fa-folder"></i>&nbsp;Add / Remove Classes</b-button>
  </div> -->
  <v-client-table :columns="columns" :data="deactivatedCaregivers" :options="options" :theme="theme" id="dataTable">
    <div slot="headings" slot-scope="props">
        {{props.headings}}
    </div>

    <div slot="name" slot-scope="props" v-if="props.row">
        {{props.row.first_name}}, {{props.row.last_name}}
    </div>
      
        
    <div slot="child_row" slot-scope="props">
      <div class="caregiver-details">
        <div class="cg-profile-heading">
          <div class="cg-profile-heading-text">Caregiver Details</div> 
          <div class="cg-profile-heading-actions">
            <b-dropdown size="sm" id="ddown_primary" text="OPTIONS" variant="primary" class="m-2">
              <b-dropdown-item @click="showActivateCaregiverModal(props.row)" >Reactivate</b-dropdown-item>
              <b-dropdown-item @click="showDeleteCaregiverModal(props.row)" >Delete</b-dropdown-item>
            </b-dropdown>
          </div>
        </div>
        
        <div class="info-boxes">
          <div class="info-box">
            <div class="info-box-column">
              <div class="info-box-item">
                <div class="info-box-info-label">Email address:</div>
                {{props.row.email_address}}
              </div>
              <div class="info-box-item">
                <div class="info-box-info-label">Hire date:</div>
                {{props.row.hire_date}}
              </div>
              <div class="info-box-item">
                <div class="info-box-info-label">Phone:</div>
                {{props.row.phone_number}}
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </v-client-table>
  <b-modal title="Are you sure?" class="modal-primary" v-model="activateCaregiverModal" @ok="activateCaregiverModal = false; activateCaregiver()" ok-variant="danger">
    Confirm that you want to activate {{selectedCaregiverName}}
  </b-modal>
  <b-modal title="Are you sure?" class="modal-danger" v-model="deleteCaregiverModal" @ok="deleteCaregiverModal = false; deleteCaregiver()" ok-variant="danger">
    Confirm that you want to delete the {{selectedCaregiverName}}
  </b-modal>
</div>
</template>

<script>
import Vue from 'vue'
import {ClientTable, Event} from 'vue-tables-2'
import { AsideToggler } from '@coreui/vue'
import {restapiPrefix} from './variables.js'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

Vue.use(ClientTable)

export default {
  name: 'DeactivedCaregiversTable',
  props: ["columns", "deactivatedCaregivers", "options","theme", "userId"],
  components: {
    ClientTable,
    Event,
    AsideToggler,
    Loading
  },
  data () {
    return {
      deleteCaregiverModal: false,
      activateCaregiverModal: false,
      selectedCaregiverName: "",
      selectedCaregiverId: "",
      selectedCaregiverUserId:"",
      isLoading: false,
      fullPage: true,
    }
  },
  methods: {
    showDeleteCaregiverModal: function (caregiver) {
      this.deleteCaregiverModal = true;
      this.selectedCaregiverName = caregiver.first_name + " " + caregiver.last_name;
      this.selectedCaregiverId = caregiver.id;
      this.selectedCaregiverUserId =  caregiver.user_id;
      // this.selectedUserId = caregiver.user_id;
    },
    showActivateCaregiverModal: function (caregiver) {
      this.activateCaregiverModal = true;
      this.selectedCaregiverName = caregiver.first_name + " " + caregiver.last_name;
      this.selectedCaregiverId = caregiver.id;
      this.selectedCaregiverUserId =  caregiver.user_id;
      // this.selectedUserId = caregiver.user_id;
    },
    activateCaregiver: function() {
      let asksUri = restapiPrefix+'/caregivers/activationCaregiverByID/'+this.selectedCaregiverId;
      this.axios.put(asksUri, {
          is_activated: 1,
        }) 
        .then(response => {
          // this.getGroupListByUserId(1);
          // console.log(response);
          miniToastr.success('Caregiver Activated Successfully!', 'Success');
          this.$emit('getDeactivatedCaregiverListByUserId', this.userId);
        })
        .catch(function (error) {
          miniToastr.error('something went wrong!', 'Error');
          console.log(error);
        });

      /*this.axios.put(asksUri).then(response => {
        console.log(response.data);
      });*/
    },
    deleteCaregiver: function() {
      let asksUri = restapiPrefix+'/caregivers/'+this.selectedCaregiverId+'/'+this.selectedCaregiverUserId;
      this.axios.delete(asksUri).then(response => {
        miniToastr.success('Caregiver deleted successfully!', 'Success');
        this.$emit('getDeactivatedCaregiverListByUserId', this.userId);
      });
    },
    rotateIcon: function (event) {
      // console.log(event)
      // var clickedElement = event.target;
      // $(clickedElement).toggleClass('down');
      event.toggleClass("down");
    },
  }
}
</script>

