<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col xs="12" lg="12" class="dashboard-content">
        <b-card>
          <template slot="title">
            <i class="icon-people"></i>
          </template>
          <b-card-body>
            <DeactivedCaregiversTable :columns="columns" :deactivatedCaregivers="deactivatedCaregivers" :options="options" :theme="theme" :userId="userId" v-on:getDeactivatedCaregiverListByUserId="getDeactivatedCaregiverListByUserId"></DeactivedCaregiversTable>
          </b-card-body>          
        </b-card>
      </b-col>
    </b-row>

    <div class="vld-parent">
        <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :is-full-page="fullPage"
        color="#06b">
