<script>
import Vue from 'vue'
import DeactivedCaregiversTable from './DeactivedCaregiversTable.vue'
import {restapiPrefix} from './variables.js'
import router from '../router';
import { ClientTable } from "vue-tables-2";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

export default {
  name: 'deactivedcaregivers',
  components: {
    DeactivedCaregiversTable,
    Loading
  },
  data: function ()  {
    return {
      columns: ['name', 'phone_number', 'deactivation_date'],
      // data: sampleData,
      userId: 1,
      deactivatedCaregivers: [],
      options: {
        headings: {
          name: 'Name',
          phoneNumber: 'Phone',
          deactivation_date: 'Deactivation Date',
        },
        sortable: ['Name'],
        filterable: ['name', 'phoneNumber'],
        sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
        pagination: {
          chunk: 5,
          edge: false,
          nav: 'scroll'
        }
      },
      useVuex: false,
      theme: 'bootstrap4',
      template: 'default',
      isLoading: false,
      fullPage: true,
    }
  },
  beforeCreate: function () {
   if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    }     
  },
  created() {
    this.isLoading = true;
    this.userId = localStorage.user_id;
    this.getDeactivatedCaregiverListByUserId(this.userId)
  },
  methods: {
    getDeactivatedCaregiverListByUserId(userId) {
      let asksUri = restapiPrefix+'/caregivers/getDeactivatedCaregiversByUserID/'+userId;
      this.axios.get(asksUri).then(response => {
        console.log(response.data)
        this.isLoading = false;
        this.deactivatedCaregivers = response.data;
      });
    },

    // rotateIcon (event) {
    //   // console.log(event)
